import { Notyf } from 'notyf';
import 'notyf/notyf.min.css';
import '../css/notify.css';

const notyf = new Notyf({
  duration: 3000,
  position: { x: 'right', y: 'top' },
  dismissible: true,
  ripple: false,
  types: [
    {
      type: 'success',
      icon: {
        className: 'notyf__icon--success',
        tagName: 'i',
      },
    },
    {
      type: 'error',
      icon: {
        className: 'notyf__icon--error',
        tagName: 'i',
      },
    },
  ],
});

export const notify = {
  success(message) {
    notyf.success(message);
  },

  error(message) {
    notyf.error(message);
  },

  warning(message) {
    notyf.open({
      type: 'warning',
      message,
    });
  },

  info(message) {
    notyf.open({
      type: 'info',
      message,
    });
  },
};
